export default {
	props:{
		length:{//长度只允许为6和4
			type:Number,
			default:6,
			validator(val){
				if(val!==6 && val !==4){
					return false;
				}
				return true;
			}
		},
		gutter:{
			type:Number,
			default:0
		},
		list:{
			type:Array,
			default:function(){
				return [];
			}
		}
	},
	data() {
		return {
		};
	},
	computed:{
		
	},
	methods:{
		formatMargin(gutter){
			return 0 + ' ' + gutter+'rpx';
		}
	}
}
